import {Component, OnInit} from '@angular/core';
import {Storage} from '@ionic/storage';
import {LoaderService} from '../../services/loader.service';
import {ApiService} from '../../services/api.service';
import {environment} from '../../environments/environment';
import * as moment from 'moment';

@Component({
    selector: 'app-locations',
    templateUrl: './locations.page.html',
    styleUrls: ['./locations.page.scss'],
})
export class LocationsPage implements OnInit {
    allchurch: any;
    churchDetails: any;
    chLat: any;
    chLong: any;
    baseURl: any;
    church_id: any;
    theme: any;
    user: any;
    enable_home: any;
    start_time_analytics: any;
    page_name: any;
    custom_name: any;
    alertMessage: any;
    page_in_home_tabs: any = false;
    constructor(
        private storage: Storage,
        private loader: LoaderService,
        private apiService: ApiService,
        private router: Router,
        private location: Location,
        private toast: ToastService,
        private analytics: AnalyticsService,
        private common: CommonService,
        private event_provider: EventProviderService,
        private platform: Platform,
        private device: Device,
        private translate: TranslateService,
    ) {
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000)

        // chekcing if page is opened in home tabs or simple
        let current_url = new URL(window.location.href);
        let page_href = current_url.pathname;
        if (page_href.includes('home-tabs')){
            this.page_in_home_tabs = true;
        } else {
            this.page_in_home_tabs = false;
        }
    }

    ngOnInit() {
        // getting theme from local storage
        this.storage.get('theme').then(async res => {
            this.theme = res;
            this.allchurch = res;
            this.church_id = res.ch_id;
            this.getUser();
            this.getChurchLocations();
            this.getAppPages()
        });
        this.baseURl = environment.image_base_url;

        this.event_provider.isHomeEnable.subscribe(value => {              // listening event
            this.enable_home = value.value;
        });
        if (!this.enable_home){
            // updating home page status from variable set in common service
            // "enable_home" variable is used to switch ON or OFF the back button on Mobile View
            // If the HOME page is disabled and THIS page is selected as first landing page, there should be no BACK button
            this.enable_home = this.common.is_home_enable;
        }
    }

    getUser() {
        // getting user from local storage
        this.storage.get('user').then((val: any) => {
            if (val != null && val != 'guest') {
                this.user = val;
            }
        });
    }

    getAppPages(){
        this.storage.get('app_pages').then(res => {
            if (res){
                // getting pagename for analytics
                let page = res.find(x => x.name === 'Locations');
                if (page){
                    this.page_name = page.name;
                    this.custom_name = page.title;
                }else {
                    this.page_name = 'Locations';
                    this.custom_name = 'Locations';
                }
            }
        });
    }

    ionViewWillEnter() {
        this.ngOnInit();

        this.event_provider.getChurchData.subscribe(async (res: any) => {
            if(!(Object.keys(res).length === 0)) {
                // asking for password
                let app_pages = res.apppages.find(x => x.name === 'Locations');
                if (app_pages.password_protection_toggle === 'true') {
                    this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
                }
            }
        });
        
    }

    getChurchLocations() {
        this.loader.presentLoading().then(() => {
        });
            // fetching church locations
            setTimeout(() => {
                this.loader.stopLoading();
            }, 1500);
            this.apiService.get_church_locations(this.church_id).subscribe(async (res: any) => {
                if (res.status) {
                    this.chLat = res.locations[0].latitude;
                    this.chLong = res.locations[0].longitude;
                    this.churchDetails = res.locations;
                    this.loader.stopLoading();
                }else{
                    this.loader.stopLoading();
                }
            }, (err: any) => {
                let message = err.message;
                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
                this.loader.stopLoading();
            });
      
    }

    openDetail(id, ch_id) {
        this.router.navigate(['location-detail', {
            id: id,
            ch_id: ch_id
        }]);
    }

    gotobackPage() {
        this.location.back();
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data

            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }


        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.user ? this.user.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.church_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }

    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
        // setting start time for analytics
        this.start_time_analytics = moment();
        this.event_provider.hidefooter(true, 'other');
    }
}

import {Router} from '@angular/router';
import {Location} from '@angular/common';
import {ToastService} from '../../services/toast.service';
import {AnalyticsService} from "../../services/analytics.service";
// import {Events} from "@ionic/angular";
import {CommonService} from "../../services/common.service";
import {EventProviderService} from "../../services/event-provider.service";
import {Platform} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {TranslateService} from "@ngx-translate/core";
